*{
  margin:0;
  padding:0;
}
.App-input
{
  text-align: center;
  background-color: rgb(181, 145, 86);
  height:300px;
  color:rgb(40, 105, 10);
  font-size: 20px;
}
.App-input input
{
font-size: 15px;
border-radius: 5px;
border:3px solid blue;
outline: none;
padding:  10px 5px;
width:200px;
}
.App-input button
{
  background-color:aqua;
  color:white;
  padding: 10px 20px;
  border-radius: 30px;
  margin-top: 10px;
}
nav{
  background-color: yellowgreen;
  padding: 10px 0;
  display: flex;
  justify-content: center;
  margin-top: 30px;
}
nav ul{
  display: flex;
  list-style-type: none;
  justify-content: space-between;
  align-items: center;
  width: 40%;
  color: white;
}
nav ul li{
  cursor:pointer;
  font-size: 14px;
  font-weight: bold;
  transition: all 0.7s ease-in-out;
}
nav ul li:hover{
  transform: scale(1.5);
}
.navActive{
  color:#1d13a3;
}
